body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: grey;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.pages{
  max-width: 1400px;
  padding: 20px;
  margin: 0 auto;
}
header .container{
max-width: 1400px;
margin: 0 auto;
padding: 10px 20px;
display: flex;
align-items: center;
justify-content: space-between;
}
h2{
  text-align: center;
}
.home{
  display: grid;
  grid-template-columns: 3fr 1fr;
  gap: 100px;
}
.profile-details{
  background: #fff;
  border-radius: 4px;
  margin: 20px auto;
  padding: 40px;
  position:relative;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.5);
}
.profile-details h4{
  margin: 0 0 10px 0;
  font-size: 1.2em;
  color: green;
}
.profile-details p{
  margin: 0;
  font-size: 0.9em;
  color: var(--primary);
  border: 4px;
  margin: 4px;
}
.profile-details span{
  position: absolute;
  top: 20px;
  right: 20px;
  cursor:pointer;
  background: #f1f1f1;
  padding: 6px;
  border-radius: 50%;
  color: #333;
  margin: 0;
  font-size: 0.9em;
  color: var(--primary);
}
form{
  background: white;
  padding:20px ;
  max-width: 580px ;
  margin:0 auto ;
  border-radius: 6px;
}
.form ,form textarea{
  display: block;
  width:100% ;
  padding:6px ;
  box-sizing:border-box ;
  border:1px solid #ccc ;
  margin:10px 0 20px 0 ;
}
form button{
  background: blue;
  color: #fff;
  border: 0;
  border-radius: 6px;
  padding: 6px 8px;
  font-family: "Poppins";
  cursor: pointer;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
img{
  border: 10px;
  display: block;
  margin-left:auto ;
  margin-right: auto;
  padding: 6px 8px;
  width:50%;
  
}
.h3{
  text-align: center;
  border: 10px;
  background: blue;
  height: 60px;
}
.h4{
  text-align: center;
}

label{
  display: block;
  width:100% ;
  padding:6px ;
  border:1px   ;
  margin:10px 0 20px 0 ;
}
.span{
  text-align: center;
}
.icon{
  padding: 10px;
  min-width: 40px;
}